body {
    font-family: sans-serif;
    background-color: var(--tg-theme-bg-color, #ffffff);
    color: var(--tg-theme-text-color, #222222);
    font-size: 16px;
    margin: 0;
    padding: 0;
    color-scheme: var(--tg-color-scheme);
}

.list-exchange {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: 250px 250px;
    transform: 50% 50%;
}
.list-exchange > * {
    border-radius: 5px;
    border: 2px solid black;
    margin: 10px;
    padding: 10px;
}

.data-exchange {
    display: flex;
    flex-direction: column;
}

.data-exchange > * {
    margin: 5px;
}

.triger-spread-green {
    background-color: rgb(28, 136, 22);
}
.triger-spread-red {
    background-color: red;
}